  --color-primary-dark: #394240;
  --color-primary-light: #A4ABA1;
  --color-secondary: #A5776C;
  --color-background: #E5DDD4;
  --color-background-alt: #C6B4A9;
  --color-text: #394240;
  --color-text-light: #BBAFA5;
  --color-accent-blue: #87CEEB;
  --color-accent-blue-dark: #4682B4;
}

